"use client";

import properties from "./data/properties.json";
import { useEffect, useRef } from "react";
import mapboxgl from "mapbox-gl";
import "mapbox-gl/dist/mapbox-gl.css"; 
import MapboxGeocoder from "@mapbox/mapbox-gl-geocoder";
import "@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css";

export default function MapPage() {
  const mapContainer = useRef(null);

  useEffect(() => {
  mapboxgl.accessToken = process.env.NEXT_PUBLIC_MAPBOX_TOKEN;

  // 1️⃣ Create the map
  const map = new mapboxgl.Map({
    container: mapContainer.current,
    style: "mapbox://styles/mapbox/streets-v12",
    center: [-111.93, 33.42], // Tempe, AZ
    zoom: 12,
  });

  // 2️⃣ Add controls (zoom, rotate, fullscreen, search)
  map.addControl(new mapboxgl.NavigationControl(), "top-right");
  map.addControl(new mapboxgl.FullscreenControl(), "top-right");
  map.addControl(
    new MapboxGeocoder({
      accessToken: mapboxgl.accessToken,
      mapboxgl: mapboxgl,
    }),
    "top-left"
  );

  // 3️⃣ Loop through JSON data and create markers
  properties.features.forEach((property) => {
    const { coordinates } = property.geometry;   // location
    const { title, description } = property.properties; // popup info

    // Make the marker
    const marker = new mapboxgl.Marker()
      .setLngLat(coordinates)
      .setPopup(
        new mapboxgl.Popup().setHTML(
          `<h3>${title}</h3><p>${description}</p>`
        )
      )
      .addTo(map);
  });

  // 4️⃣ Cleanup
  return () => map.remove();
}, []);


  return (
    <div style={{ height: "100vh", width: "100vw", position: "relative" }}>
      {/* Title bar */}
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          background: "black",
          opacity: 0.7,
          color: "white",
          textAlign: "center",
          padding: "8px",
          zIndex: 1,
        }}
      >
        Zonix | Tempe Zoning Map
      </div>

      {/* Map container */}
      <div ref={mapContainer} style={{ height: "100%", width: "100%" }} />
    </div>
  );
}
